.app {
  position: relative;
  text-align: center;
  background-image: url('../public/assets/img/lab-bg.webp');
  background-size: cover;
  background-position: center center;
  background-repeat: no-repeat;
  width: 100vw;
  height: 100vh;
}

.app-filter {
  position: absolute;
  top: 0;
  left: 0;
  width: 100vw;
  height: 100vh;
  background-color: rgba(0, 0, 0, 0.5);
}

.bg-center {
  background-position: center center;
  background-repeat: no-repeat;
  background-size: contain;
}

.label {
  font-size: 1em;
  font-weight: 700;
  color: #ffe600;
}

.vessel-flex-container {
  width: calc(100% - 4px);
  max-width: 800px;
  height: 92%;
  margin: auto;
}

.vessel-wrapper {
  width: 188px;
  height: 240px;
  display: block;
  position: relative;
}

.vessel {
  width: 100%;
  height: 100%;
  background-image: url('../public/assets/img/bejana.webp');
  background-size: contain;
  background-repeat: no-repeat;
  background-position: center bottom;
  position: absolute;
  display: block;
}

.vessel-water {
  width: 100%;
  height: 100%;
  display: block;
  position: absolute;
  background-image: url('../public/assets/img/air-bejana.webp');
  background-position: center bottom;
  background-repeat: no-repeat;
  background-size: contain;
  left: 2px;
}

.vessel-full-container {
  width: 200px;
  height: 450px;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}

.glass-box {
  background-image: url('../public/assets/img/aquarium.webp');
}

.liquid-tube {
  background-image: url('../public/assets/img/tabung-liquid-2.webp');
}

.termometer {
  background-image: url('../public/assets/img/termometer.webp');
}

.propeller {
  background-image: url('../public/assets/img/baling.webp');
  transition: all 1s linear;
}

.propeller-stick {
  background-image: url('../public/assets/img/tiang-baling.webp');
  transition: all 1s linear;
}

.calculator {
  background-image: url('../public/assets/img/calculator.webp');
}

.screen {
  background-image: url('../public/assets/img/screen.webp');
}

.machine {
  background-image: url('../public/assets/img/mesin.webp');
}

.sample-tube {
  background-image: url('../public/assets/img/botol-sample.webp');
}

.sample-tube-cap {
  background-image: url('../public/assets/img/tutup-botol.webp');
}

.syringe {
  background-image: url('../public/assets/img/spelt.webp');
}

.syringe-holder {
  background-image: url('../public/assets/img/tutup-spelt.webp');
}

.vessel-full {
  background-image: url('../public/assets/img/desolusi.webp');
}

.termometer-full {
  background-image: url('../public/assets/img/termometer-2.webp');
}

.ctm {
  background-image: url('../public/assets/img/CTM.webp');
}

.blink {
  animation: blinker 1s linear infinite;
}

@keyframes blinker {
  50% {
    opacity: 0;
  }
}

.rotate {
  animation: rotator 0.5s linear infinite;
}

@keyframes rotator {
  50% {
    transform: translateX(-50%) scaleX(-1);
  }
}

.calculator-input {
  font-size: 12px;
  width: 40px;
  background-color: #9c996d;
  outline: none;
  border: none;
  border-radius: 5px;
  margin-top: 24px;
  margin-left: -35px;
  padding-right: 5px;
  text-align: right;
  color: #ffe600;
  caret-color: #ffe600;
}

.calculator-key-container {
  display: grid;
  padding: 8px 10px 0;
  gap: 5px;
  grid-template-columns: repeat(4, 1fr);
}

.calculator-btn-digit {
  background-color: transparent;
  color: transparent;
  border: none;
  cursor: pointer;
}

.calculator-btn-cancel {
  grid-column: 1/-1;
}
